<template id="search">
  <div>
    <h2>Find Available</h2>

    <p>City: {{ parkinglot.location.city }}</p>
    <p>Address: {{ parkinglot.location.address }} {{ parkinglot.location.number }}</p>
    <p>ZipCode: {{ parkinglot.location.zipcode }}, {{ parkinglot.location.area }}</p>

    <br>

    <form @submit.prevent="search" method="post">

      <p>
        Date <input type="date" id="datePicker">
      </p>

      <p>
        Time
        <select id="selectTime"></select>
      </p>

      <p>
        Type
        <select id="typeSelect">
          <option>All types</option>
        </select>
        <br>
        <br>
      </p>

      <p>
        <label for="hours">Hours you wish to book for </label><br>
          <input v-model="hours" type="number" name="hours" min="1" id="hours" required="required"
                 oninput="validity.valid||(value='');">
      </p>

      <p>
        <input type="submit" value="Search">
      </p>
    </form>

    <p>
      Choose An Available Spot To Book
      <select id="spotSelect"></select>
    </p>

    <button @click="this.book">Book</button>

  </div>

</template>

<script>
import {postRequest} from "@/restApi/ApiCaller";

export default {
  name: "Search",
  data: () => ({
    parkinglot: null,
    spots: [],
    date: null,
    time: null,
    hours: null,
    alreadyDone: false,
    alreadyDoneType: false,
    selectTime: null,
    typeSelect: null,
    spotSelect: null,
  }),created() {
    this.getLot();
    this.fillSelectWith24Hours();
    this.fillSelectWithTypes();
  }, methods: {
    getLot() {
      fetch(`http://localhost:8080/api/parkinglot/get/${this.$route.params.id}`, {
        credentials: 'include'
      }).then(response => response.json())
          .then(data => this.parkinglot = data)
          .catch(error => alert('Error:', error));
    },
    zeroPad(num, places) {
      return String(num).padStart(places, '0')
    },
    fillSelectWith24Hours() {
      this.selectTime = document.getElementById("selectTime");
      for (let i = 0; i < 24; i++) {
        let newHour = document.createElement("option");
        newHour.text = this.zeroPad(i, 2);
        this.selectTime.add(newHour);
      }
    },
    fillSelectWithTypes() {
      this.typeSelect = document.getElementById("typeSelect");
      let spots = this.parkinglot.spots;
      let types = [];
      for (let i = 0; i < spots.length; i++) {
        if(types.indexOf(spots[i].type) === -1){
          types.push(spots[i]);
        }
      }
      for (let i = 0; i < types.length; i++) {
        let newType = document.createElement("option");
        newType.text = types[i];
        this.typeSelect.add(newType);
      }
    },
    search() {
      this.date = document.getElementById("myDate").value;
      this.time = this.selectTime.options[this.selectTime.selectedIndex].text;
      this.date += ` ${this.time}`;
      this.typeSelect = document.getElementById("typeSelect");
      let type = this.typeSelect.options[this.typeSelect.selectedIndex].text;
      fetch(`http://localhost:8080/api/booking/onlyavailable/${this.$route.params.id}/${type}/${this.date}/${this.hours}`)
          .then(res => res.json())
          .then(res => {
            this.spots = res;
            this.fillSelectWithSpots();
          })
          .catch(error  => alert("Error:", error ));
      },
    book() {
      let spotId = this.spotSelect.options[this.spotSelect.selectedIndex].value;
      let bookingInfo = [spotId, this.parkinglot.id, this.date, this.hours];
      postRequest("api/booking/book", bookingInfo);
    },
    fillSelectWithSpots() {
      this.spotSelect = document.getElementById("spotSelect");

      for (let i = 0; i < this.spots.length; i++) {
        let newSpot = document.createElement("option");
        newSpot.value = this.spots[i].id;
        newSpot.text = `Type ${this.spots[i].type}, Hourly Price ${this.spots[i].hourlyPrice}`;
        this.spotSelect.add(newSpot);
      }
    }
  }
}
</script>